.header {
  //box-sizing: border-box;
  background-image: url(/src/img/Cover_photo.png);
  background-size: cover;
  //height: 100vh;
  background-position: 82%;
  background-repeat: no-repeat;




  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;

    @include content-paddihg-inline;
  }

  &__title {
    text-align: center;
    margin: 0;
    line-height: 36px;
    font-style: Poppins;
    font-weight: 700;
    font-size: 36px;
    letter-spacing: -1.5px;

    @include on-tablet {
      font-size: 64px;
    }

    @include on-desktop {
      font-size: 80px;
    }
  }

  &__bottom {
    padding-bottom: 88px;

  @include on-tablet {
    padding-bottom: 130px;
  }

  @include on-desktop {
    padding-bottom: 203px;
  }
    //@include content-paddihg-inline;
  }
}
