.contacts {


  &__field {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 20px;
    max-width: 1200px;
    display: grid;

    @include on-tablet {
      grid-template-columns: repeat(6, 1fr);
      column-gap: 24px;
    }

    @include on-desktop {
      grid-template-columns: repeat(12, 1fr);
    }
  }

  &__form {
    grid-column: 1 / -1;
    display: grid;

    @include on-tablet {
      grid-column: 1 / 4;
    }

    @include on-desktop {
      grid-column: span 5;
    }
  }

  &__form-input {
    background: #faf9f8;
    font-family: Poppins;
    border: 1px solid #f8f8fa;
    border: none;
    border-radius: 40px;
    height: 48px;
    padding: 0 20px;
    outline: none;
    margin-bottom: 16px;
    padding-left: 16px;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
  }

  &__form-textarea {
    resize: none;
    background: #f8f8fa;
    border-radius: 16px;
    margin-bottom: 32px;
    padding-top: 14px;
    padding-left: 16px;
    font-family: Poppins;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    height: 148px;

    &:focus {
      outline: none;
    }
  }

  &__form-button {
    color: #292929;
    text-align: center;
    cursor: pointer;
    background-color: #fff;
    border: none;
    border-radius: 40px;
    margin-bottom: 48px;
    font-size: 16px;
    font-weight: 700;
    line-height: 56px;
    text-decoration: none;
    transition: box-shadow 0.3s;

    &:hover {
      box-shadow: 1px 4px 16px #e8e8f066;
    }

    &:focus {
      transition: box-shadow 0.3s;
    }

    &:focus:hover {
      box-shadow: 1px 4px 16px #e8e8f0a3;
    }
  }

  &__detail {
    margin-left: 30px;
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 4 / 6;
    }

    @include on-desktop {
      grid-column: 8 / 11;
    }
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: grid;
  }

  &__list-target {
    color: #fff;
    text-decoration: none;
    transition: color 0.4s;

    &:hover {
      color: #e8e8f066;
    }
  }
}

.contacts__list li:nth-child(2n) {
  text-align: left;
  padding-bottom: 24px;
  font-size: 16px;
  font-weight: 500;
  line-height: 22px;
}

.contacts__list li:nth-child(odd) {
  text-align: left;
  padding-bottom: 8px;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
}


